section.ba-bg {
  background-image: url("../img/ba-cover-bg.jpg");
  background-size: cover;
  background-position: center;
  padding: $ms3 $ms1;
  position: relative;

  min-height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  & h2, h3, p {
    color: white;
  }
}

a.link-on-red {
  color: palette(yellow, 100);

  &:active {
    transition: all .2s ease-in;
  }

    &:hover {
      color: palette(dark-blue, 100);
    }
}
