h1, h2, {
  font-family: $secondary-font;
  margin-bottom: $ms0;
}

p {
  line-height: 1.8;
  margin-top: 0;
  margin-bottom: $ms1;

  @include breakpoint(xl) {
    @include fluid-type(font-size, 80.06rem, 100rem, $ms0, 1.25rem);
  }
}

.footnote {
  font-size: .9rem;
  line-height: 1.8;
}

h1 {
  font-size: $ms8;
  font-weight: normal;
  color: white;
  text-align: left;
  line-height: 1.2;

  @include breakpoint(xs) {
    font-size: $ms10;
  }
}

h2 {
  font-size: $ms6;
  font-weight: normal;
  text-transform: uppercase;
  color: #d3314f;
  text-align: left;
  margin-bottom: 0px;
}

h3 {
  font-size: $ms3;
  font-weight: normal;
  text-transform: uppercase;
  margin-top: 15px;
  margin-bottom: 0px;
  color: #d3314f;

  @include breakpoint(sm) {
    line-height: 1.8;
  }
}

h4 {
  font-size: $ms3;
  font-weight: normal;
  margin-bottom: 0px;
  margin-top: 15px;
  color: $offblack;
  padding-bottom: 30px;

  @include breakpoint(sm) {
    line-height: 1.8;
}
}


.divider-red {
  border-top: 2px solid #d3314f;
}

.divider-yellow {
  border-top: 2px solid #ffd530;
}

.divider-xs {
  @extend %divider;
  width: 17%;
}

.divider-sm {
  @extend %divider;
  width: 22%;
}

.divider-studycontact {
  @extend %divider;
  width: 27%;
}

.divider-md {
  @extend %divider;
  width: 30%;
}

.divider-lg {
  @extend %divider;
  width: 72%;
}

.divider-strong {
  @extend %divider;
  width: 66%;
}

.divider-arms {
  @extend %divider;
  width: 42%;
}

.divider-outcome {
  @extend %divider;
  width: 33%;
}
