// Containers
.wrapper {
  @include j-center(100%);
  background: palette(red, 100);
  height: 100%;

  @include breakpoint(md) {

  }
}

main.container {
  height: auto;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-image: url('../img/dot-circle-bg.svg');
}

// Generic container
.content-container {
  @include j-center(1440px);
}

// Grid

// Wrap all columns so floats are cleared after.
.col-wrap, .full, .halves, .thirds  {
  @include j-cf();
  height: 100%;
}

.one-fifth {
  @include j-stack();
  height: 100%;

  @include breakpoint(sm) {
    @include j-unstack();
    @include j-column(1/3, $gutter: 0, $cycle: 3);

    &:nth-child(4), &:nth-child(5) {
      @include j-column(1/2, $gutter: 0);
    }
  }

  @include breakpoint(lg) {
    @include j-column(1/5, $gutter: 0, $cycle: 5);

    &:nth-child(4), &:nth-child(5) {
      @include j-column(1/5, $gutter: 0);
    }
  }
}

.one-quarter {
  @include j-stack();
  height: 100%;

  @include breakpoint(sm) {
    @include j-unstack();
    @include j-column(1/2, $gutter: 0, $cycle: 2);
  }

  @include breakpoint(lg) {
    @include j-column(1/4, $gutter: 0, $cycle: 4);
  }
}

.one-third{
  @include j-stack();
  height: 100%;

  @include breakpoint(sm) {
    @include j-unstack();
    @include j-column(1/3, $gutter: 0);
  }
}

.two-thirds {
  @include j-stack();
  height: 100%;

  @include breakpoint(sm) {
    @include j-unstack();
    @include j-column(2/3, $gutter: 0);
  }
}

.one-half {
  @include j-stack();
  height: 100%;

  @include breakpoint(sm) {
      @include j-unstack();
      @include j-column(1/2, $gutter: 0);
    }
}
