section.intro-section {
  background-image: url("../img/cover-bg.jpg");
  background-size: cover;
  background-position: center;
  padding: $ms3 $ms1;
  position: relative;

  min-height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;


  @include breakpoint(xl) {
    margin-top: 0;
    height: 100% !important;
    padding: $ms1;
  }

  & h3, ul {
    color: white;
  }

  /*@media all and (min-width: 1441px) and (max-height: 800px) {
    min-height: 1000px;
  }*/

 & .intro-content {
   //@include fluid-type(font-size, 30rem, 125rem, $ms0, $ms1);
   width: 100%;

   & p {
     margin-bottom: $ms0 !important;
     color: white;
   }

  /* @include breakpoint(md) {
     max-width: 55rem;
     margin: 0 auto;
   }

   @include breakpoint(xxl) {
     max-width: 50rem;
   }*/
  }
}
