html {
  box-sizing: border-box;
  font-family: $main-font;
  height: 100%;
}


*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: $text-color;
  // font-feature-settings: "kern" 1, "dlig" 1; // Turns on kerning and ligatures
  // -moz-font-feature-settings: "kern" 1, "dlig" 1;
  // -ms-font-feature-settings: "kern" 1, "dlig" 1;
  height: 100%;
}

img {
  max-width: 100%;
}

%divider {
  margin-bottom: $ms4;
  height: 0;
}

a, a:visited, a:active {
  color: palette(red, 100);
  transition: all .2s ease-in;

  &:hover {
    color: palette(light-blue, 100);
  }
}
