header {
  position: fixed;
  display: none;
  left: 0;
  top: 0;
  height: 100%;
  z-index: 100;
  width: 100%;
  opacity: 0;

  @include breakpoint(md) {
    display: block;
    height: 75px;
    top: 0;
    border-bottom: 10px solid rgba(white, .70);
    opacity: 1;
  }
}

nav {
  background: rgba(white, .97);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include breakpoint(md) {
    background: transparent;
    display: block;
    height: 66px;
  }
}

nav ul {
  @include j-cf();
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  @include j-stack();
  text-align: center;

  @include breakpoint(md) {
    @include j-column(1/5, $cycle: 6, $gutter: 0);
    display: inline;
  }

  &:last-child {
    border-right: 0;
  }
}

// nav action //
a.nav {
    color: $offblack;
    text-decoration: none;
    width: 100%;
    display: block;
    font-family: $main-font;
    text-transform: uppercase;
    font-size: $ms1;
    font-weight: bold;
    line-height: 66px;
    background-color: white;

    @include breakpoint(md) {
     background-color: white;
      @include fluid-type(font-size, 30rem, 66.25rem, $ms0, $ms1);
      // padding: $ms0 $ms-1;
    }

    @include breakpoint(lg) {
      // padding: $ms1 $ms0
    }

    &:hover {
      color: palette(red, 100);
    }

    &:active {
      color: palette(red, 100);
    }
  }



// Mobile nav
.mobile-header {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(white, .97);
  z-index: 101;
  width: 100%;
  text-align: right;
  padding: 0 $ms1;
  height: 44px;

  @include breakpoint(md) {
    display: none;
  }
}

header.mobile-menu-open {
  display: block;
  opacity: 1;
  transition: opacity .2s ease-in;
}

.toggle-nav-button {
  font-family: $secondary-font;
  color: $offblack;
  text-decoration: none;
  font-size: $ms1;
  z-index: 101;
  line-height: 54px;
  height: 44px;

  @include breakpoint(md) {
    display: none;
  }
}

.show-desktop {
  display: none;

  @include breakpoint(md) {
    display: block;
  }
}

.show-mobile {
  display: block;
  margin-top: -17px;

  @include breakpoint(md) {
    display: none;
  }

}
//icon
[data-icon]:before {
  font-family: "icons-font-1" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "icons-font-1" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-ellipsis-h:before {
  content: "\61";
}
.icon-ellipsis-v:before {
  content: "\62";
}
.icon-bars:before {
  content: "\63";
}
