// $Fonts
// ==========================================================================
@import url('https://fonts.googleapis.com/css?family=Khand:500|Open+Sans:400,400i,700,700i');

// FF Tisa
@font-face {
	font-family: "ff-tisa-web-pro";
	src: url('../fonts/TisaWeb.eot');
	src: url('../fonts/TisaWeb.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "ff-tisa-web-pro";
	src: url('../fonts/TisaWeb-Italic.eot');
	src: url('../fonts/TisaWeb-Italic.woff') format('woff');
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: "ff-tisa-web-pro";
	src: url('../fonts/TisaWeb-Bold.eot');
	src: url('../fonts/TisaWeb-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "ff-tisa-web-pro";
	src: url('../fonts/TisaWeb-BoldItalic.eot');
	src: url('../fonts/TisaWeb-BoldItalic.woff') format('woff');
	font-weight: bold;
	font-style: italic;
}

@font-face {
  font-family: "icons-font-1";
  src:url("../fonts/icons-font-1.eot");
  src:url("../fonts/icons-font-1.eot?#iefix") format("embedded-opentype"),
    url("../fonts/icons-font-1.woff") format("woff"),
    url("../fonts/icons-font-1.ttf") format("truetype"),
    url("../fonts/icons-font-1.svg#icons-font-1") format("svg");
  font-weight: normal;
  font-style: normal;
}

$main-font: 'Open Sans', Helvetica, Arial, sans-serif;
$secondary-font: 'ff-tisa-web-pro', Georgia, serif;
$accent-font: 'ff-tisa-web-pro', Georgia, serif;
