// ==========================================================================
// $Variables
// ==========================================================================

// $Colors
// ==========================================================================
$offblack:      #333;
$gray:          #ccc;
$gray-light:    lighten($gray, 10%);
$gray-dark:     darken($gray, 10%);

// LGFCU Color Palette

$palettes: (
	light-blue: (
		100: #197ab5,
		75: #4091c2,
		50: #7fb5d6,
		25: #bfd9ea
	),

	dark-blue: (
		100: #005c8d,
		75: #4086aa,
		50:	#7faec6,
		25:	#bfd6e2
	),

	light-green: (
		100: #a1b950,
		75: #adc560,
		50: #c8d896,
		25: #e5edd0
	),

	dark-green: (
		100: #007a33,
		75: #409d6a,
		50: #7fbd9b,
		25: #bfdecd
	),

	red: (
		100: #d3314f,
		75: #f24070,
		50: #f67f9f,
		25: #fbbfcf
	),

	orange: (
		100: #f9a348,
		75: #fbb16e,
		50: #fccb9e,
		25: #fee5ce
	),

	purple: (
	  100: #69528f,
		90: #816da0,
		75: #866ea4,
		50: #8975a8,
		25: #ae9ec2
	),

	yellow: (
		100:#ffe376,
		25: #fff4c8
	),

	gray:	(
		100: rgba(157,159,162,1),
		75: #b6b7b9,
		50: #cecfd0,
		25: #e6e7e8
	)
);

$text-color: 		$offblack;

// $Breakpoints
// ==========================================================================
$breakpoints: (
	'xs': 480px,
	'sm': 600px,
  'md': 960px,
	'lg': 1100px,
	'xl': 1441px,
	'xxl' : 1600px,
	'tab' : 768px
);

// $Box Shadows
// ==========================================================================
$box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);


// $Modular_Scale
// ==========================================================================
// Create your own at modularscale.com

$ms12: 7.594rem;
$ms11: 7.478rem;
$ms10: 5.61rem;
$ms9: 5.063rem;
$ms8: 4.209rem;
$ms7: 3.375rem;
$ms6: 3.157rem;
$ms5: 2.369rem;
$ms4: 2.25rem;
$ms3: 1.777rem;
$ms2: 1.5rem;
$ms1: 1.333rem;
$ms0: 1rem;
$ms-1: 0.75rem;
$ms-2: 0.667rem;
