section#arrow {
  background-image: url("../img/triangle-divider-yellow.svg");
  background-position: center -1px;
  background-attachment: scroll;
  background-repeat: no-repeat;
  -webkit-background-size: 10%;
  background-size: 10%;
  margin-bottom: 10px;

  @include breakpoint(md) {
    background-size: 100px;
  }
}

.light-yellow-25 {
  background-color: palette(yellow, 25);
}
table {
  line-height: 1.8;
  margin-top: 0;
  margin-bottom: $ms1;
}
table th {
  color: #d3314f;
  font-weight: 600;
  padding: 8px;
  background-color: #ffe376;
  border: 2px solid white;
}

table td {
  padding: 20px;
  border: 2px solid #ffe376;
}
table tr:first-child td {
  border-top: 0;
}
table tr td:first-child {
  border-left: 0;
}
table tr:last-child td {
  border-bottom: 0;
}
table tr td:last-child {
  border-right: 0;
}

ul, ol{
  margin-top: 0px;
}
li {
  line-height: 1.8;
  padding-bottom: 8px;
}

.yellow-bg-title {
  color: #d3314f;
  font-weight: 600;
  text-align: center;
  padding: 8px;
  background-color: #ffe376;
}

.vl {
    border-left: 2px solid $offblack;
    height: 100px;
}
