.yellow-100 {
  background-color: #ffe376;
  height: auto;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-image: url('../img/dot-circle-bg.svg');
}

h3.span{
  color:$offblack;
}


.contact-form-container {
  max-width: 960px;
  margin: 0 auto;
  background-color: white;
  color: $offblack;
  margin-bottom: -100px;

  & .one-half {
    padding: $ms0 $ms0;
    font-weight: bold;
  }

  @include breakpoint(xl) {
    max-width: 1600px;
    padding: $ms8;
  }
}

.reach-us-contact-info {
  margin: $ms4 auto;

  @include breakpoint(sm) {
    max-width: 70%;
  }

  @include breakpoint(xl) {
    max-width: 50%;
  }
}

/* form starting stylings ------------------------------- */
.form-first {
  margin-top: 35px;
}

.group        {
  position:relative;
  padding-bottom: 45px;
}

.submit-btn {
width: 30%;
color: white;
background-color: palette(red, 100);
border: none;

  &:hover {
  background-color: palette(dark-blue, 100);
  }
}

input  {
  font-size:18px;
  padding:10px 10px 10px 5px;
  display:block;
  min-height: 50px;
  width: 100%;
}

textarea {
  display: block;
  resize: none;
  width: 100%
}

/* LABEL ======================================= */
label          {
  font-size:18px;
  font-weight:normal;
  position:absolute;
  pointer-events:none;
  left:10px;
  top:10px;
  transition:0.2s ease all;
}

/* active state */
input:focus ~ label, input:valid ~ label    {
  top:-30px;
  font-size:14px;

}

textarea:focus ~ label, textarea:valid ~ label    {
  top:-30px;
  font-size:14px;

}

a.btn {
  background-color: $gray-dark;
  color: #333;
  display: inline-block;
  font-weight: bold;
  padding: 10px 15px;
  text-decoration: none;
  &:hover {
    background-color: $gray;
  }
}
